@charset "utf-8";
$bg-blue:#242F9B;

.header {
  width: 100%;
  height: 80px;
  background-color:$bg-blue;
  color: #ffffff;
  display: flex;
  margin-bottom: 30px;
  @media(min-width:768px) {
    justify-content: space-between;
  }
}
header a {
  text-decoration: none;
  color:#fff;
  white-space: nowrap;
}
.sign-out-button {
  color: #ffffff;
  margin: auto 5% auto auto;
}
header h1 {
  padding:15px 0 0 5%;
  margin-left: 24px;
}
