@charset "utf-8";
$form-bg:#555;
$red: #ff3333;
$bg-blue: #242F9B;

.edit-list {
  width: 1000px;
  margin: 0 auto;
}

.edit-list-form {
  color: $form-bg;
}

.edit-list-title {
  width: 300px;
  margin-bottom: 25px;
}

.delete-list-button {
  color: $red;
  border: solid 1px $red;
  width: 80px;
  height: 40px;
  margin: 25px 5px;
  border-radius: 5px;
}

.edit-list-button {
  background-color: $bg-blue;
  color: #ffffff;
  width: 80px;
  height: 40px;
  border-radius: 5px;
}

.error-message {
  color: $red;
}