.taskList {
  margin: 50px auto;
  list-style: none;
  width: 90%;
  padding: 0;
}

.list-header {
  display: flex;
  justify-content: space-between;
  @media(max-width:767.9px) {
    width: 100%;
    flex-direction: column;
    justify-content: center;
    text-align: left;
  }
}

.list-menu {
  text-align: right;
}

.list-tab {
  list-style: none;
  display: flex;
  overflow-x: auto;
  width: 100%;
  flex-wrap: wrap;
  @media(max-width:767.9px) {
    text-align: center;
    margin: 10px auto;
  }
}

.list-tab-item {
  flex-shrink: 0;
  width: 100px;
  padding: 0 15px;
  text-align: center;
  margin: 3px 5px;
  border: solid 1px #242F9B;
  justify-content: center;
  align-items: center;
  &:hover {
    cursor: pointer;
  }
  @media(min-width: 768px) {
    width: 150px;
    padding: 5px 25px;
    font-size:20px;
    margin: 7.5px 15px;
  }
}

.active {
  background-color: #242F9B;
  color: #ffffff;
}

.tasks {
  margin-top: 25px;
}

.tasks-header {
  display: flex;
  justify-content: space-between;
}

.task-item {
  list-style-type: none;
  height: 80px;
  width: calc(100% - 25px);
  background-color: #f5f5f5;
  padding:5px 0 5px 25px;
  font-size: large;
  border: solid 1px #ffffff;
  margin: 10px 0;
}

.task-item-link {
  display: block;
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: #000000;
}

.error-message {
  color: #ff3333;
}

.display-select-wrapper {
  width: 100px;
  margin: 0 0 25px auto;
}

.display-select {
  width: 100%;
}